'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { getUserHeaderData } from '@/lib/actions';
import { type HeaderData } from '@/ai/schemas';
import { Skeleton } from '@/components/ui/skeleton';
import { HeaderForm } from '@/components/header-form';
import { useToast } from '@/hooks/use-toast';

function HeaderSkeleton() {
  return (
    <div className="w-full">
      <div className="flex items-center justify-between p-6 pb-4 border-b">
        <div className="flex items-center gap-4">
          <Skeleton className="h-6 w-24" />
          <Skeleton className="h-10 w-[250px]" />
        </div>
        <Skeleton className="h-10 w-32" />
      </div>
      <div className="p-6">
        <div className="text-center mb-6">
          <Skeleton className="h-8 w-64 mx-auto" />
        </div>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          <Skeleton className="h-96 rounded-lg" />
          <Skeleton className="h-96 rounded-lg" />
          <Skeleton className="h-96 rounded-lg" />
        </div>
      </div>
    </div>
  )
}

export default function HeaderPage() {
  const router = useRouter();
  const { toast } = useToast();
  const [headerData, setHeaderData] = useState<HeaderData[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const databaseId = sessionStorage.getItem('databaseId');

    if (!databaseId) {
      toast({
        title: 'Authentication Error',
        description: 'You must be logged in to view this page. Redirecting to login.',
        variant: 'destructive'
      });
      router.push('/login');
      return;
    }

    async function fetchData() {
      setIsLoading(true);
      const result = await getUserHeaderData(databaseId!);
      if (result.success && result.data) {
        setHeaderData(result.data);
      } else {
        toast({
          title: 'Failed to Load Data',
          description: result.error || 'Could not retrieve header information.',
          variant: 'destructive',
        });
      }
      setIsLoading(false);
    }

    fetchData();
  }, [router, toast]);

  if (isLoading) {
    return <HeaderSkeleton />;
  }

  return <HeaderForm allData={headerData} />;
}
