'use client';

import { useForm, type SubmitHandler } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { type HeaderData, HeaderDataSchema } from '@/ai/schemas';

interface HeaderFormProps {
    allData: HeaderData[];
}

export function HeaderForm({ allData }: HeaderFormProps) {
    const form = useForm<HeaderData>({
        resolver: zodResolver(HeaderDataSchema),
        defaultValues: {}, // Initially empty
    });

    const handleTinSelect = (selectedTin: string) => {
        const selectedData = allData.find(d => d.tpTIN === selectedTin);
        if (selectedData) {
            form.reset(selectedData); // Populate form with selected data
        }
    };

    const onSubmit: SubmitHandler<HeaderData> = (data) => {
        // Handle form submission, e.g., save data to the sheet
        console.log(data);
    };

    return (
        <div className="bg-white border-l h-full w-full">
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)}>
                    <div className="flex items-center justify-between p-6 pb-4 border-b">
                        <div className="flex items-center gap-4">
                            <Label htmlFor="tin-select" className="text-sm font-medium">Load TIN:</Label>
                            <Select onValueChange={handleTinSelect}>
                                <SelectTrigger id="tin-select" className="w-[250px]">
                                    <SelectValue placeholder="Select TIN to load data" />
                                </SelectTrigger>
                                <SelectContent>
                                    {allData.map(data => (
                                       <SelectItem key={data.tpTIN} value={data.tpTIN!}>
                                            {data.tpTIN} - {data.companyName || `${data.lastName}, ${data.firstName}`}
                                       </SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>
                        <Button type="submit">Save Header</Button>
                    </div>

                    <div className="p-6">
                        <h2 className="text-xl font-bold text-center mb-6">Taxpayer Information</h2>
                        <div className="grid grid-cols-1 gap-8 md:grid-cols-3">
                            <Card>
                                <CardHeader>
                                    <CardTitle className="text-lg">Tax ID and Other Details</CardTitle>
                                </CardHeader>
                                <CardContent className="space-y-4">
                                    <FormField control={form.control} name="tpTIN" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Tax Identification Number</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                     <FormField control={form.control} name="branchCode" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Branch Code</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                     <FormField control={form.control} name="rdoCode" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>RDO Code</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                    <FormField control={form.control} name="entityType" render={({ field }) => (
                                         <FormItem>
                                            <FormLabel>Classification</FormLabel>
                                            <Select onValueChange={field.onChange} value={field.value} key={field.value}>
                                                <FormControl>
                                                    <SelectTrigger><SelectValue placeholder="Select Classification" /></SelectTrigger>
                                                </FormControl>
                                                <SelectContent>
                                                    <SelectItem value="Non-Individual">NON-INDIVIDUAL</SelectItem>
                                                    <SelectItem value="individual">INDIVIDUAL</SelectItem>
                                                </SelectContent>
                                            </Select>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                    <FormField control={form.control} name="cycleType" render={({ field }) => (
                                         <FormItem>
                                            <FormLabel>Reporting Cycle</FormLabel>
                                            <Select onValueChange={field.onChange} value={field.value} key={field.value}>
                                                <FormControl>
                                                    <SelectTrigger><SelectValue placeholder="Select Cycle" /></SelectTrigger>
                                                </FormControl>
                                                <SelectContent>
                                                    <SelectItem value="calendar">CALENDAR</SelectItem>
                                                    <SelectItem value="fiscal">FISCAL</SelectItem>
                                                </SelectContent>
                                            </Select>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                     <FormField control={form.control} name="monthSelect" render={({ field }) => (
                                         <FormItem>
                                            <FormLabel>Fiscal Month</FormLabel>
                                            <Select onValueChange={field.onChange} value={field.value} key={field.value}>
                                                <FormControl>
                                                    <SelectTrigger><SelectValue placeholder="Select Month" /></SelectTrigger>
                                                </FormControl>
                                                <SelectContent>
                                                     <SelectItem value="1">JANUARY</SelectItem>
                                                     <SelectItem value="2">FEBRUARY</SelectItem>
                                                     <SelectItem value="3">MARCH</SelectItem>
                                                     <SelectItem value="4">APRIL</SelectItem>
                                                     <SelectItem value="5">MAY</SelectItem>
                                                     <SelectItem value="6">JUNE</SelectItem>
                                                     <SelectItem value="7">JULY</SelectItem>
                                                     <SelectItem value="8">AUGUST</SelectItem>
                                                     <SelectItem value="9">SEPTEMBER</SelectItem>
                                                     <SelectItem value="10">OCTOBER</SelectItem>
                                                     <SelectItem value="11">NOVEMBER</SelectItem>
                                                     <SelectItem value="12">DECEMBER</SelectItem>
                                                </SelectContent>
                                            </Select>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                </CardContent>
                            </Card>

                            <Card>
                                <CardHeader>
                                    <CardTitle className="text-lg">Registered Name</CardTitle>
                                </CardHeader>
                                <CardContent className="space-y-4">
                                     <FormField control={form.control} name="companyName" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Company Name</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                    <FormField control={form.control} name="lastName" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Last Name</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                    <FormField control={form.control} name="firstName" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>First Name</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                    <FormField control={form.control} name="middleName" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Middle Name</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                    <FormField control={form.control} name="tradeName" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Trade Name</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                </CardContent>
                            </Card>

                            <Card>
                                <CardHeader>
                                    <CardTitle className="text-lg">Registered Address</CardTitle>
                                </CardHeader>
                                <CardContent className="space-y-4">
                                    <FormField control={form.control} name="subStreet" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Sub-street</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                     <FormField control={form.control} name="street" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Street</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                     <FormField control={form.control} name="barangay" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Barangay</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                     <FormField control={form.control} name="cityMunicipality" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>City/Municipality</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                     <FormField control={form.control} name="province" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Province</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                     <FormField control={form.control} name="zipCode" render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Zip Code</FormLabel>
                                            <FormControl><Input {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )} />
                                </CardContent>
                            </Card>
                        </div>
                    </div>
                </form>
            </Form>
        </div>
    );
}
